<template class="back">
    <form action="" class="login-container">
        <p>Login</p>
        <input type="text" placeholder="用户名"
        v-model="username"
        oninput="value=value.replace(/[^\w_]/g,'')" maxlength="10"><br/>
        <input type="password" placeholder="密码"
        v-model="password"
        oninput="value=value.replace(/[^\w_]/g,'')" maxlength="10"><br/>
        <label @click="register">注册点这里!</label><br/>
        <span @click="handleLogin" class="btn">登录</span>
    </form>
</template>

<script>
export default {
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    handleLogin () {
      if (this.username === '' || this.password === '') {
        alert('请输入完整哟！(゜▽＾*))')
      } else {
        this.$router.push({ path: './notes' })
        // alert('欢迎！♪(^∇^*)')
      }
    },
    register(){
      alert('因为没给后端接口，所以随便输就能进哦！ (๑•̀ㅂ•́)و✧')
    }
  }
}
</script>

<style scoped>

    .login-container{

    position : absolute;
    top: 50px;
    margin-top: 150px;
    left: 50%;
    margin-left: -400px;
    background-color: whitesmoke;
    width: 300px;
    height: 400px;
    border-radius: 25px;
    opacity: 80%;
    }
    p{
    font-size: 42px;
    font-weight: 600;
}

input{
    background-color: whitesmoke;
    width: 100px;
    height: 48px;
    transform: translate(100%,-50%);
    text-align:center;
    margin-bottom: 10px;
    border: none;
    border-bottom: 2px solid silver;
    outline: none;
    font-size: 22px;
}

.btn{
    background-color: #59c2c5;
    width: 38%;
    height: 48px;
    transform: translate(90%,-50%);
    border-radius: 8px;
    margin-top: 40px;
    font-size: 28px;
    font-weight: 600;
    color: white;
    margin-left: 120px;
}
.btn:hover{
    background-color: #59c2a0;

}

label {
  margin-left: 110px;
  color:  rgb(145,178,253);
}
</style>
